@import "styles/variables";

.wrapper {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
  padding: 3rem;
}

.logo {
  width: 100%;
  filter: grayscale(1);
  transition-duration: $transitionDuration;

  &:hover {
    filter: grayscale(0);
  }
}

@media (max-width: $adaptiveMax) {
  .wrapper {
    padding: 1rem 1.5rem;
  }
}