@import "styles/mixins";
@import "styles/variables";

.wrapper {
  height: 80rem;
}

.bg {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-size: cover;
}

.content {
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  padding: 12rem 28rem;
  height: 100%;
  z-index: 2;
}

.description {
  @include regularBanner($colorLightest, 700);

  margin: 2rem 0 0 0;
  width: 50%;
}

@media (max-width: $adaptiveMax) {
  .content {
    justify-content: center;
    align-items: center;
    padding: 0 7rem;
  }

  .heading {
    font-size: 4rem;
    line-height: 6rem;
  }
}