@import "styles/variables";

.wrapper {
  position: relative;
  padding-right: 15rem;
  padding-left: 15rem;
}

.slider {
  margin-top: 6rem;
  padding-bottom: 5rem;
}

.image {
  width: 100%;
}

.fullImage {
  max-width: 90vw;
  max-height: 90vh;
}

.button {
  position: absolute;
  top: 23rem;

  &:not(.buttonNext) {
    left: 0;
  }

  &.buttonNext {
    right: 0;
  }
}

@media (max-width: $adaptiveMax) {
  .wrapper {
    padding-right: 0;
    padding-left: 0;
    margin-right: -3rem;
    margin-left: -3rem;
  }

  .heading {
    padding: 0 3rem;
  }

  .slider {
    margin-top: 2rem;
    padding-right: 6rem;

    :global {
      .swiper-wrapper {
        padding-left: 3rem;
      }

      .swiper-slide {
        width: 20rem;
      }
    }
  }

  .slideButton {
    width: 20rem;
    height: 28rem;
    overflow: hidden;
  }
}