@import "styles/variables";

.wrapper {
  overflow: hidden;
}

.container {
  padding-bottom: 8rem;
}

.list {
  margin-top: 6rem;
}

.filter {
  padding-top: 8rem;
  padding-bottom: 6rem;
}

@media (max-width: $adaptiveMax) {
  .container {
    padding-bottom: 0;

    .list {
      margin-top: 3rem;
      margin-right: -3rem;
      margin-left: -3rem;
    }

    .filter {
      padding-top: 2rem;
      padding-bottom: 2rem;
    }
  }
}