@import "../../../styles/variables";
@import "../../../styles/mixins";

.wrapper {
  position: relative;
  height: 25rem;
  overflow: hidden;
}

.overlay {
  position: relative;
  height: 70rem;
  background-size: cover;
  background-position: center;
}

@media (max-width: $adaptiveMax) {
  .wrapper {
    height: 16rem;
  }
}