@import "styles/variables";
@import "styles/mixins";

.wrapper {
  --banner-height: 81rem;
  margin-bottom: -1rem;
  height: var(--banner-height);
  overflow: hidden;
}

.container {
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: flex-start;
  height: calc(var(--banner-height) - 1rem);
  background-size: cover;
  background-repeat: no-repeat;
}

.heading {
  @include visuallyHidden;
}

.button {
  @include resetButton;
  @include h2($colorLightest);
}

.indicator {
  position: relative;

  &::before {
    content: "";
    position: absolute;
    top: 3.2rem;
    right: -4.5rem;
    border-top: 1.2rem solid transparent;
    border-left: 2rem solid $colorLightest;
    border-bottom: 1.2rem solid transparent;
    transform: translateX(0);
    transition-duration: $transitionDuration;
    transition-property: transform;
    pointer-events: none;
  }

  &:hover::before {
    transform: translateX(-2rem);
  }

  &:active::before {
    transform: translateX(0);
  }
}

.containerList {
  position: absolute;
  top: 0;
  right: calc(50% - 94rem / 1.4);
  width: 94rem;
  height: calc(var(--banner-height) - 1rem);
  transform: translateY(-110%);
  transition-duration: $transitionDuration;
  transition-property: transform;
  box-shadow: -0.6rem 0.6rem 0.5rem rgba(0, 0, 0, 0.2);
  z-index: 1;
}

.containerListOpened {
  transform: translateY(0);
}

.list {
  @include resetList;

  display: flex;
  flex-direction: column;
  justify-content: space-between;
  padding: 11rem 13rem 15rem 13rem;
  width: 100%;
  height: 100%;
  background-color: $colorHighlightLight;
}

.link {
  @include lang;

  display: block;
  color: $colorLightest;
  text-transform: uppercase;

  &::before {
    top: 0;
  }
}

@media (max-width: $adaptiveMax) {
  .button {
    font-size: 3rem;
    line-height: 4rem;
  }

  .indicator {
    position: relative;

    &::before {
      top: 1.6rem;
      right: -4.5rem;
      border-top: 0.8rem solid transparent;
      border-left: 1.4rem solid $colorLightest;
      border-bottom: 0.8rem solid transparent;
    }

    &:hover::before {
      transform: translateX(-1rem);
    }
  }

  .containerList {
    right: 0;
    width: 100%;
  }

  .buttonClose {
    position: absolute;
    top: 2rem;
    right: 5.5rem;
  }

  .list {
    padding: 8rem 10rem 8rem 7rem;
  }

  .link {
    font-size: 1.8rem;
    line-height: 2.7rem;

    &::before {
      top: 0.5rem;
    }
  }
}